  --manatee-clr: #A5A5B7;
  --transparent-white-clr-1: #ffffffb8;
  --wild-blue-yonder-clr: #B9B9D9;
  --primary-clr: #FF9122;
  --primary-dark-clr: #F09872;
  --primary-light-clr: #FFB973;
  --imperial-red-clr: #e2494c;
  --mantis-clr: #7ec062;
  --celadon-green-clr: #128C7E;
  --space-cadet-clr: #33335D;
  --independence-clr-1: #343C54;
  --royal-purple-clr: #7555B7;
  --denim-clr: #0059B2;
  --sunglow-clr: #ffd24d;
  --orange-yellow-crayola-clr: #fdd365;
  --black-clr: #000;
  --onyx-clr: #33383A;
  --silver-clr: #C2C2C2;
  --davys-grey-clr: #4B5254;
  --eerie-black-clr-2: #222;
  --eerie-black-clr: #1C2124;
  --black-coral-clr: #646573;
  --independence-clr: #535362;
  --eerie-black-clr-1: #161A1D;
  --light-slate-gray-clr: #7C8E9A;
  --transparent-black-clr: #0000001a;
  --transparent-white-clr: #ffffff3b;
}



/* prefer color scheme */

// Dark theme 
@media (prefers-color-scheme: dark) {
  :root {
    --nav-clr: #cacaca;
    --white-clr: #292b2e;
    --independence-clr: #cdcfd2;
    --independence-clr-1: #e3796a;
    --black-coral-clr: #e2e3e7;
    --wild-blue-yonder-clr: #e2e3e7;
    --primary-dark-clr: #86786b;
    --space-cadet-clr: #84a0ef;
    --denim-clr: #082D53;
    --mantis-clr: #537c42;
    --orange-yellow-crayola-clr: #997e36;
    --royal-purple-clr: #382958;
    --cultured-clr-1: #6D6054;
    --onyx-clr: var(--manatee-clr);
    // --mantis-clr: #86786b;
  }

  img.dark {
    filter: brightness(75%);
  }

  .landing.home {
    background-image: url("../images/mobile-landing-dark.png") !important;
    @include breakpoint(laptop) {
      background-image: url("../images/landing-dark.png") !important;
    }
  }
  .landing.webtasarim {
    background-image: url("../images/webtasarim-landing-dark.png") !important;
