/* --- Start Global Rules ---*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
body::-webkit-scrollbar-track {
  background-color: var(--gray-clr)
}
body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: var(--primary-clr);
}
body::-webkit-scrollbar-corner {
  border-radius: 10px;
  background-color: var(--primary-clr);
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;

  // -- tablet screens -- //
  @include breakpoint(tablet) {
    width: 750px;
  }

  // -- laptop screens -- //
  @include breakpoint(laptop) {
    width: 970px;
  }

  // -- desktop screens -- //
  @include breakpoint(desktop) {
    width: 1170px;
  }

  // -- tv screens -- //
  @include breakpoint(tv) {
    width: 1370px;
  }
}

video {
  height: auto;
  max-width: 100%;
}

img {
  height: auto;
  max-height: 100%;
  max-width: 100%;
}

ul {
  list-style: none;
}
/* --- End Global Rules ---*/
